import React, { useState } from "react";
import listaPlanetas from "./planetas";
import Planeta from "./Planeta";

function App() {
  const [planetas, setPlanetas] = useState(listaPlanetas);
  const [dinheiro, setDinheiro] = useState(0);
  const nome = [ 
    ""," Mil"," Milhão"," Bilhão"," Trilhão"," Quadrilhão"," Quintilhão"," Sextilhão", " Septilhão"
  ]
  let copiaDinheiro = dinheiro;
  let contador = 0;

  function alteraPlaneta(index, propriedade, valor) {
    const copiaPlanetas = planetas;
    copiaPlanetas[index][propriedade] = valor;

    setPlanetas(copiaPlanetas);
  }
  function arrumaDinheiro(){
    if(copiaDinheiro>1000){
      copiaDinheiro/=1000;
      contador ++;
      arrumaDinheiro();
    }
  }
  

  return (
    <div>
      <div id="dinheiroPosition">
        <div id="dinheiro">$ 
          {arrumaDinheiro()}
          {(copiaDinheiro).toFixed(1)}
          {nome[contador]}
          
        </div>
      </div>
      
      <div id="planetasContainer">
        {planetas.map((planeta, index) => (
          <Planeta
            alteraPlaneta={(propriedade, valor) =>
              alteraPlaneta(index, propriedade, valor)
            }
            data={planeta}
            setDinheiro={setDinheiro}
            dinheiro={dinheiro}
          />
        ))}
      </div>
    </div>
  );
}

export default App;
