import React, { useState } from "react";

const cores = [
  "#181818", "#708090", "#CD853F", "#C0C0C0", "#DAA520", "#3CB371", "#EE82EE", "#8B008B", "#C71585", "#CD5C5C"
];
const sufixo = [
 "","K","M","B","T","Qa","Qi","Sx","Sp"
]

function Planeta({ alteraPlaneta, setDinheiro, dinheiro, data }) {
  let copiaLucro = data.lucro*data.bases;
  let maxContador = 0;
  let copiaBases = data.investimento;
  let contador = 0;

  function arrumaDinheiro(){
    if(copiaLucro>=1000){
      copiaLucro/=1000;
      alteraPlaneta("contador", data.contador + 1);
      arrumaDinheiro();
    }

    if(maxContador<data.contador)
      maxContador=data.contador

    alteraPlaneta("contador", 0);

    return (copiaLucro).toFixed(1);
  }

  function arrumaBases(){
    if(copiaBases>=1000){
      copiaBases/=1000;
      alteraPlaneta("contadorInvest", data.contadorInvest + 1);
      arrumaBases();
    }
    if(contador<data.contadorInvest)
      contador=data.contadorInvest
    alteraPlaneta("contadorInvest", 0);

    return parseInt(copiaBases);
  }

  return (
    <div id="content">
      <div id="wrap">
        <div>
          {" "}
          {/* nome */}
          {data.nome}
          {" +"}
          {data.level}
        </div>

        <div id="middle">
          <button
            style={{
              borderColor: cores[parseInt(data.bases/5)],
              backgroundColor: cores[parseInt(data.bases/5)]
            }}
            id="botaoPlaneta"
            onClick={() => {
              if (data.inicialClicks >= data.clicks) {
                alteraPlaneta("lucro", data.lucro * 1.3);
                alteraPlaneta("clicks", data.clicks * 1.5);
                alteraPlaneta("inicialClicks", 0);
                alteraPlaneta("level", data.level+1);
              } 
              else {
                alteraPlaneta("inicialClicks", data.inicialClicks + 1);
              }

              setDinheiro(dinheiro + data.lucro * data.bases);
            }}
          >
            <img id="imagem" src={data.imagem} />
          </button>
           + {arrumaDinheiro()}{sufixo[maxContador]}
          
          <button
            id="investir"
            onClick={() => {
              if (dinheiro > data.investimento) {
                alteraPlaneta("bases", data.bases + 1);
                alteraPlaneta("lucro", data.lucro*1.5);
                setDinheiro(dinheiro - data.investimento);
                alteraPlaneta("investimento", data.investimento * 2);
              }
          }}>
            
            + Bases ${arrumaBases()}{sufixo[contador]}
          </button>
        </div>
        <div id="footer">
          <span>{parseInt((100 * data.inicialClicks) / data.clicks)}%</span>
          <span>Bases: {data.bases}</span>
        </div>

        <div>{}</div>
      </div>
    </div>
  );
}

export default Planeta;
